@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,700;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ingredient-list {
  margin-top: 1rem;
  background-color: #f0f0f0;
  border-radius: 0.5rem;
  padding: 0.5rem;
  border: 2px solid var(--secondary-color);
}

.ingredient-list li {
  margin: 0.5rem 0;
}

.ingredient-text {
  font-size: 1.5rem;
  color: rgba(0, 0, 0, 0.6);
}

.ingredient-weight {
  color: var(--primary-color);
  font-size: 1.3rem;
}

.ingredients-div {
  animation: show 1s linear forwards;
}

@keyframes show {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

/* -----------------------------------------------------------------MEDIA QUERIES-------------------------------------------------------- */

@media only screen and (max-width: 1200px) {
  .ingredient-text,
  .ingredient-weight {
    font-size: 1rem;
  }
}

@media only screen and (max-width: 800px) {
  .ingredient-text,
  .ingredient-weight {
    font-size: 0.8rem;
  }
}

.healthLabel_ul {
  margin-top: 1rem;
  background-color: #f0f0f0;
  border-radius: 0.5rem;
  padding: 0.5rem;
  border: 2px solid var(--secondary-color);
}

.healthLabel_li {
  margin: 0.5rem 0;
  font-size: 1.5rem;
  color: rgba(0, 0, 0, 0.6);
}

.healthLabel_div {
  animation: show 1s linear;
}

/* -----------------------------------------------------------------MEDIA QUERIES-------------------------------------------------------- */

@media only screen and (max-width: 1200px) {
  .healthLabel_li {
    font-size: 1rem;
  }
}

@media only screen and (max-width: 800px) {
  .healthLabel_li {
    font-size: 0.8rem;
  }
}

.recipes {
  width: 90%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
}

.recipe {
  width: 35rem;
  min-height: 95vh;
  margin: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex: 1 1;
  background-color: beige;
  padding: 2rem;
  position: relative;
  border-radius: 0.5rem;
  box-shadow: 0.2rem 0.2rem 0.5rem black;
  transition: transform 0.3s ease-in;
}

.recipe:hover {
  transform: scale(1.05);
}

.recipe h1 {
  color: var(--secondary-color);
  margin-top: 0;

  font-size: 2rem;
}

.recipe img {
  border-radius: 0.5rem;
  object-fit: cover;
  transition: border-radius 0.5s ease-in;
}

.recipe img:hover {
  border-radius: 50%;
  border: 2px solid var(--secondary-color);
}

.recipe a {
  font-size: 1.8rem;
  color: var(--primary-color);
  margin-bottom: 1rem;
}

.recipe p {
  font-size: 1.5rem;
  margin: 1rem 0;
}

.recipe h3,
.recipe button {
  display: inline;
}
.recipe h3 {
  text-transform: uppercase;
  font-size: 1.5rem;
  letter-spacing: 1px;
  margin-right: 1rem;
}

.recipe button {
  cursor: pointer;
  padding: 0 1rem;
  font-size: 1.5rem;
  text-transform: uppercase;
  color: #fff;
  background-color: var(--primary-color);
  border: none;
  border-radius: 0.5rem;
}

.health_labels {
  margin-top: 1rem;
}

/* -------------------------------------------------------------MEDIA QUERIES------------------------------------------------------- */

@media only screen and (max-width: 1200px) {
  .recipe a {
    font-size: 1.5rem;
  }

  .recipe h1 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    margin-top: 1rem;
  }
  .recipe h3 {
    font-size: 1.2rem;
  }
  .recipe p {
    font-size: 1.2rem;
  }

  .recipe img {
    height: 13rem;
  }
  .recipe {
    padding-top: 0;
    width: 35vw;
    height: auto;
    margin: 1.6vw;
  }
}

@media only screen and (max-width: 930px) {
  .recipe {
    margin: 1.2rem;
    width: 40vw;
    height: auto;
  }
}

@media only screen and (max-width: 800px) {
  .recipe {
    margin: 1rem;
    min-width: 30vw;
    padding: 0.2rem;
  }

  .recipe img {
    height: 8rem;
  }
  .recipe p,
  .recipe h3 {
    font-size: 0.8rem;
    padding: 0;
  }
  .recipe a {
    font-size: 1rem;
  }
  .recipe button {
    font-size: 1rem;
    padding: 0 0.7rem;
  }
}

@media only screen and (max-width: 600px) {
  .recipe {
    margin: 1rem;
    min-width: 50vw;
    padding: 0.2rem;
  }

  .recipe h1 {
    font-size: 1.8rem;
  }
  .recipe img {
    height: 12rem;
  }
  .recipe p,
  .recipe h3 {
    font-size: 1.4rem;
    padding: 0;
  }
  .recipe a {
    font-size: 1.3rem;
  }
  .recipe button {
    font-size: 1rem;
    padding: 0.4rem 1.2rem;
  }
}

@media only screen and (max-width: 350px) {
  .recipe button {
    font-size: 0.5rem;
    padding: 0.3rem 1rem;
  }

  .recipe p,
  .recipe h3 {
    font-size: 1rem;
    letter-spacing: normal;
  }
}

* {
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  padding: 0;
  list-style-type: none;
  outline: none;
  --primary-color: #06573c;
  --secondary-color: #483d8b;
}

html {
  font-size: 1rem;
  background-color: var(--secondary-color);
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  font-size: 4rem;
  letter-spacing: 0.1rem;
  color: beige;
  text-transform: uppercase;
  margin: 4rem 0;
}

.search-form {
  width: 50%;
  border: none;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.5rem;
  background-color: rgba(0, 0, 0, 0.25);
  position: relative;
}

.search-form input {
  font-size: 1.6rem;
  border: none;
  border-bottom: 0.1rem solid #ccc;
  margin-right: 0.5rem;
  border-radius: 0.5rem;
  height: 3rem;
}

.search-form input[type='text'] {
  padding: 0 0.5rem;
  font-style: italic;
  width: 70%;
  color: black;
  text-align: center;
}

.search-button {
  background-color: var(--primary-color);
  font-size: 1.4rem;
  border: none;
  border-radius: 0.5rem;
  padding: 0.7rem;
  color: white;
  letter-spacing: 2px;
  width: 30%;
  cursor: pointer;
}

/* ----------------------------------------------MEDIA QUERIES------------------------------------------------------- */

@media only screen and (max-width: 1200px) {
  h1 {
    font-size: 3rem;
  }
  .search-form input {
    font-size: 1.2rem;
  }
}

@media only screen and (max-width: 930px) {
  h1 {
    font-size: 2.5rem;
  }
  .search-form input {
    font-size: 1rem;
  }
  .search-button {
    font-size: 1.2rem;
  }
}

@media only screen and (max-width: 800px) {
  h1 {
    font-size: 2rem;
  }
  .search-form input {
    font-size: 0.9rem;
  }
  .search-button {
    font-size: 1rem;
  }
}

@media only screen and (max-width: 600px) {
  h1 {
    font-size: 1.2rem;
    padding: 0 0.5rem;
    text-align: center;
  }

  .search-form {
    width: 80%;
  }
  .search-form input {
    font-size: 0.8rem;
  }
  .search-button {
    font-size: 0.9rem;
  }
}

